import styled from 'styled-components';
import { ReducedMotion } from 'framer-motion';

export const ProjectImage = styled.img`
  overflow: hidden;
  object-fit: contain;
  border: 2px solid black;
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
`;

export const Wrapper = styled.div`
  height: auto;
  width: auto;
`;
